<template>
	<div class="Personal">
		<div class="p-inner">
			<div class="p-gradent"></div>
			<div id="g-user">
				<section class="p-info">
					<div class="p-image">
						<img src="https://i0.hdslb.com/bfs/face/4c4850ff0fbf8cc10db6f30f335446fcd5f24c54.jpg_64x64.jpg" alt="">
						<span title="" class="user-auth"></span>
					</div>
					<div class="p-basic">
						<section>
							<h3 id="name">{{name}}</h3>
							<a href="" target="_blank" class="p-level m-level" :lvl="level"></a>
						</section>
						<div class="p-sign">
							<p :title="sign">{{sign}}</p>
						</div>
					</div>
				</section>
				<section class="p-option">
					<button class="p-option-follow" id="follow" v-show="nufollow"  @click="perfollow()"></button>
					<button class="p-option-followed" id="followed" v-show="followed"  @click="perfollowed()"></button>
					<button class="p-option-send"></button>
					<div id="p-option-after">
						<a title="" class="iconfont icon-ic_more" @mouseenter="showBox()" @mouseleave="hiddenBox()"></a>
						<transition name="after">
							<div class="p-option-after-hidden" v-if="afterShow" @mouseenter="showBox()" @mouseleave="hiddenBox()">
								<ul class="hidden-ul-item">
									<li v-for="(item, index) in afterArr" :key="index">{{item}}</li>
								</ul>
							</div>
						</transition>
					</div>
				</section>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name:'Personal',
		data() {
			return {
				name:'',
				sign:'    ',
				level:2,
				afterShow: false,
				followed: false,
				nufollow: true,
				afterArr:['','','']
			}
		},
		methods: {
			showBox() {
				this.afterShow = true;
			},
			hiddenBox(){
				this.afterShow = false;
			},
			// 
			perfollow(){
				if(sessionStorage.getItem('session_phone') != null){
					document.getElementById("follow").innerHTML = ""
					localStorage.setItem('follow', "true");
					this.$router.go(0)
				}else{
					alert('')
				}
				
			},
			perfollowed(){
				document.getElementById("followed").innerHTML = ""
				localStorage.setItem('follow', "false");
				this.$router.go(0)
			}
		},
		created() {
		// ,,,
			if(localStorage.getItem('follow') == "true" && sessionStorage.getItem('session_phone') != null){
				this.followed = true
				this.nufollow = false
			}else if(localStorage.getItem('follow') == "false"){
				this.followed = false
				this.nufollow = true
			}
		}
	}
</script>

<style>
	@import url("../css/init.css");
	/*  */
	@import url("../css/indexContentTop.css");
</style>
